<style lang="less">
@import "../../assets/styles/login.less";
</style>
<template>
  <f7-page class="login" no-toolbar no-navbar no-swipeback login-screen>
    <f7-login-screen-title>
      <div class="logo"></div>
      比亚迪CCRM系统
    </f7-login-screen-title>
    <f7-list form>
      <f7-list-input
        type="text"
        placeholder="请输入用户名"
        :value="username"
        @input="username = $event.target.value"
      ></f7-list-input>
      <f7-list-input
        type="password"
        placeholder="请输入密码"
        :value="password"
        @input="password = $event.target.value"
      ></f7-list-input>
    </f7-list>
    <f7-list>
      <f7-button class="color-red" raised big fill @click="signIn">登录</f7-button>
    </f7-list>
  </f7-page>
</template>
<script>
import Cookies from "js-cookie";
export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    signIn() {
      const self = this;
      if (!this.username) {
        $$.alert("请输入用户名");
        return;
      }
      Cookies.set("user", this.username);
      this.$f7router.navigate("/#/", {
        reloadCurrent:true,
        clearPreviousHistory: true
      });
    }
  }
};
</script>



