<template>
<div class="basic-userinfo no-navbar no-toolbar">
    <div class="title">早上好，{{userName}}</div>
    <div class="sub-title">进步之于产品，解决之于出行</div>
</div>
</template>
<script>
import Cookies from "js-cookie";
export default{
    data(){
        return {
            userName:''
        }
    },
    created(){
        this.userName = Cookies.get('user')
    }
}    
</script>


